const getters = {
    margin_style(state) {
        return state.response ? 'margin:0 0 0 230px' : 'margin:0 0 0 75px';
    },

    getJSon: (state) => (data, callback) => {
        data = JSON.stringify(data);
        var xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function() {
            if (xhr.readyState === 4 && xhr.status == 200) {
                callback(JSON.parse(xhr.response));
            }
        }
        xhr.open('POST', `${state.APIURL}api.php`, true);
        xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
        xhr.send(data);

    },

    UPLOADFILE: (state) => (max, callback) => {
        var files = document.getElementById("userfile").files[0];
        var reader = new FileReader();
        reader.onloadend = function() {
            var tempImg = new Image();
            tempImg.src = reader.result;
            tempImg.onload = function() {
                var MAX_WIDTH = max[0];
                var MAX_HEIGHT = max[1];
                var tempW = tempImg.width;
                var tempH = tempImg.height;
                if (tempW > tempH) {
                    if (tempW > MAX_WIDTH) {
                        tempH *= MAX_WIDTH / tempW;
                        tempW = MAX_WIDTH;
                    }
                } else {
                    if (tempH > MAX_HEIGHT) {
                        tempW *= MAX_HEIGHT / tempH;
                        tempH = MAX_HEIGHT;
                    }
                }
                var canvas = document.createElement('canvas');
                canvas.width = tempW;
                canvas.height = tempH;
                var ctx = canvas.getContext("2d");
                ctx.drawImage(this, 0, 0, tempW, tempH);
                var dataURL = canvas.toDataURL("image/jpeg");
                var xhr = new XMLHttpRequest();
                xhr.onreadystatechange = function() {
                    if (xhr.readyState === 4 && xhr.status == 200) {
                        callback(JSON.parse(xhr.response));

                    }
                }
                xhr.open('POST', `${state.APIURL}upload.php`, true);
                xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
                var data = `image=${dataURL}&up=home`;
                xhr.send(data);
            }
        }
        reader.readAsDataURL(files);

    },


    API: (state) => (url, callback, def = false) => {
        def = !def ? state.APIURL + url : url;
        state.modal_Container = 'loaders';
        fetch(def).then(response => response.json())
            .then(data => callback(data));

    },

    createfetch(state) {
        Object.keys(state.tap).forEach(function(key) {
            fetch(`${state.APIURL}get.php?action=${key}`).then(response => response.json())
                .then(data => updateObj(key, data));
        });

        function updateObj(key, data) {
            if (key == 'platfrom') {
                state.tap.platfrom = data
            } else {
                state.tap.sections = data
            }
        }
    },

    send: (state) => (e, data, ref) => {
        switch (e) {
            case 'data':
                data = data == 1 ? 'true' : 'erorr';
                state.modal_Container = data;
                if (data == 'erorr') return;
        }
        if (ref) {
            switch (ref) {
                case 'web_design':
                    state.ref = `<a href="/design/">الانتقال الي العروض</a>`;
                    break;
                case 'mark':
                    state.ref = `<a href="/marketing/">الانتقال الي البافات</a>`;
                    break;
                case 'server':
                    state.ref = `<a href="/servers/">الانتقال الي الخطط</a>`;
                    break;
            }
        } else {
            state.modal_Container = false;
            return 1;
        }
    },



    remove: (state) => (arr, i) => {
        switch (arr) {
            case 'foods':
                state.foodsArr.splice(i, 1);
                break;
            case 'ingredient':
                state.ingredient.splice(i, 1);
                break;
            case 'workouts':
                state.workouts.splice(i, 1);
                break;
            case 'meal':
                if (state.search.length > 0) {
                    state.search.splice(i, 1);
                    for (let ind in state.itemsMeal) {
                        if (state.itemsMeal[ind].id == i) {
                            state.itemsMeal.splice(ind, 1);
                        }
                    }
                    for (let ind in state.search) {
                        if (state.search[ind].id == i) {
                            state.search.splice(ind, 1);
                        }
                    }
                } else {
                    state.itemsMeal.splice(i, 1);
                }
                break;
        }
    },
};

export default getters;