import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

let routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '*',
        component: () =>
            import ('../pages/404.vue')
    },
    {
        path: '/users',
        component: () =>
            import ('../pages/users.vue')
    },
    {
        path: '/users/edit',
        name: 'users',
        component: () =>
            import ('../pages/edit/users.vue'),
    },
    {
        path: '/users/add',
        name: 'users',
        component: () =>
            import ('../pages/add/users.vue'),
    },
    {
        path: '/foods/add',
        name: 'foods/add',
        component: () =>
            import ('../pages/add/foods.vue'),
    },
    {
        path: '/foods/edit',
        name: 'foods/edit',
        component: () =>
            import ('../pages/add/foods.vue'),
    },
    {
        path: '/foods',
        name: 'foods',
        component: () =>
            import ('../pages/foods.vue'),
    },
    {
        path: '/foods/setup',
        name: 'foods/setup',
        component: () =>
            import ('../pages/setup/food-setup.vue'),
    },

    {
        path: '/Workouts/add',
        name: '/Workouts/add',
        component: () =>
            import ('../pages/add/Workouts.vue'),
    },
    {
        path: '/Workouts/edit',
        name: 'updates',
        component: () =>
            import ('../pages/edit/update.vue'),
    },
    {
        path: '/Workouts',
        name: 'Workouts',
        component: () =>
            import ('../pages/Workouts.vue'),
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router