<template>
    <div v-if="$store.state.modal_Container" id="modal_Container">
        <div class="model_content">
            <!--erorr-->
            <div v-if="checkModel('erorr')">
                <h3>حدث خطا برجاء المحاوله في وقت لاحق</h3>
            </div>

            <!--loader-->
            <div v-if="checkModel('loaders')" class="loaders"></div>

            <!--done-->
            <div v-if="checkModel('true')">
                <i class="fas fa-check"></i>
                <div v-html="ref()"></div>
            </div>

            <!--delete-->
            <div v-if="checkModel('remove')">
                <h3>هل تريد الحذف نهائياً؟</h3>
            </div>

            <div class="op">
                <b v-if="checkModel(['remove','erorr'])" @click="closeModel()">الغاء</b>
                <b v-if="checkModel('remove')" @click="Setmodel()">مسح</b>
            </div>
            <!--end-->

        </div>
    </div>
</template>

<script>
    export default {
        name: "AlertApp",
        data() {
            return {
   
            }
        },
        components: {},
        methods: {
            ref() {
                return this.$store.state.ref;
            },
            Setmodel() {
                var ajax = this.$store.state.ajax;
                this.changeModel('loaders');
                this.$store.getters.API(`remove.php?table=${ajax.table}&id=${ajax.id}`, data => {
                    this.oki(data, ajax);
                });
            },
            checkModel(value) {
                var model = this.$store.state.modal_Container;
                switch (typeof value) {
                    case "string":
                        if (model == value) {
                            return true;
                        }
                        break;
                    case "object":
                        for (let i of value) {
                            if (i == model) return true;
                        }
                }
                return false
            },
            changeModel(value) {
                this.$store.state.modal_Container = value;
            },
            closeModel() {
                this.changeModel(false);
                this.$store.state.ajax = {};
            },
            oki(response, ajax) {
                if (response) {
                    this.$store.getters.remove(ajax.table, ajax.i);
                    this.closeModel();
                } else {
                    this.changeModel('erorr');
                }
            },
        },
    }
</script>

